





























import Vue from 'vue'
import axios from 'axios'


export default Vue.extend({
    name: "Projects",
    components: {
    },
    data: function(){
        return{        
            ProjectsData: null
        }

    },
    beforeMount:
    function(){
      axios
        .get(this.$data.ServerLink +'/getProjects?language=ru')
        .then(response => {
          if (response.data.status=="error")
          console.log(response.data.message);
          else
          this.ProjectsData = response.data;
        })
        .catch(error => {
          console.log(error);
        })
  },
    methods:{

  } 
})
